@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Source+Serif+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: #415691;
  text-decoration: none;
  transition: color ease-out 200ms;
}
a:visited {
  color: #2a3961;
}
a:hover {
  color: #a5a5a5;
}

.App {
    font-family: 'Josefin Sans', sans-serif;
}

.fade-enter {
    opacity: 0.01;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
}
.fade-leave {
  opacity: 1;
}
.fade-leave.fade-leave-active {
  opacity: 0.01;
  transition: opacity 200ms ease-in;
}

.fade-long-enter {
    opacity: 0.01;
}
.fade-long-enter.fade-long-enter-active {
    opacity: 1;
    transition: opacity 600ms cubic-bezier(0.77, 0, 0.175, 1);
}
.fade-long-leave {
  opacity: 1;
}
.fade-long-leave.fade-long-leave-active {
  opacity: 0.01;
  transition: opacity 200ms cubic-bezier(0.77, 0, 0.175, 1);
}

.header-move-0-enter {
  -webkit-transform: translate(-108px, 32px);
          transform: translate(-108px, 32px);
}
.header-move-0-enter.header-move-0-enter-active {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}
.header-move-0-leave {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.header-move-0-leave.header-move-0-leave-active {
  -webkit-transform: translate(-108px, 32px);
          transform: translate(-108px, 32px);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.header-move-1-enter {
  -webkit-transform: translate(-36px, 32px);
          transform: translate(-36px, 32px);
}
.header-move-1-enter.header-move-1-enter-active {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}
.header-move-1-leave {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.header-move-1-leave.header-move-1-leave-active {
  -webkit-transform: translate(-36px, 32px);
          transform: translate(-36px, 32px);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.header-move-2-enter {
    -webkit-transform: translate(36px, 32px);
            transform: translate(36px, 32px);
}
.header-move-2-enter.header-move-2-enter-active {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: -webkit-transform 200ms ease-in;
    transition: transform 200ms ease-in;
    transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}
.header-move-2-leave {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.header-move-2-leave.header-move-2-leave-active {
  -webkit-transform: translate(36px, 32px);
          transform: translate(36px, 32px);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.header-move-3-enter {
  -webkit-transform: translate(108px, 32px);
          transform: translate(108px, 32px);
}
.header-move-3-enter.header-move-3-enter-active {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}
.header-move-3-leave {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.header-move-3-leave.header-move-3-leave-active {
  -webkit-transform: translate(108px, 32px);
          transform: translate(108px, 32px);
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.HomePage {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
@media only screen and (min-width: 1024px) {
  .HomePage {
    flex-direction: row;
  }
}

.HomePage-backdrop {
  height: 40vh;
  object-position: center 25%;
  object-fit: cover;
  transition: opacity linear 300ms;
}
@media only screen and (min-width: 1024px) {
  .HomePage-backdrop {
    width: 50%;
    height: 100vh;
    object-position: top;
  }

  .faded-out {
    opacity: 0;
  }
}

.HomePage-content {
  position: relative;
  height: 60vh;
  max-width: 500px;
  padding: 4em 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
}
@media only screen and (min-width: 1024px) {
  .HomePage-content {
    text-align: end;
    width: 50%;
    height: 100vh;
    margin: 0 auto;
    padding: 3em;
  }
}

.HomePage-login, .HomePage-login:visited {
  cursor: pointer;
  color: #a5a5a5;
  text-align: right;
  font-size: 0.75em;
  opacity: 0.02;
  padding: 1em 2em;
  position: absolute;
  top: 12px;
  right: 24px;
  border: dashed 1px #a5a5a5;
  border-radius: 20px;
  transition: opacity ease-in-out 200ms;
}
@media only screen and (min-width: 1024px) {
  .HomePage-login {
    top: 96px;
    right: 0;
  }
}
.HomePage-login:hover {
  opacity: 0.25;
}
.HomePage-login-shown, .HomePage-login-shown.HomePage-login:hover {
  opacity: 1;
}

.HomePage-user-widget {
  height: 3em;
  width: 3em;
  position: absolute;
  top: 12px;
  right: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: dashed 1px #a5a5a5;
  border-radius: 50%;
}
@media only screen and (min-width: 1024px) {
  .HomePage-user-widget {
    top: 96px;
    right: 0;
  }
}

.HomePage-user-photo {
  height: 2em;
  width: 2em;
  margin: 0 auto;
  border: solid 3px #d7d7eb;
  border-radius: 50%;
}

.HomePage-title {
  font-size: 3em;
}
@media only screen and (min-width: 1024px) {
  .HomePage-title {
    font-size: 4em;
  }
}

.HomePage-subtitle {
  color: #444444;
  font-size: 0.75em;
  letter-spacing: 2px;
  padding: 1em 0;
}
@media only screen and (min-width: 1024px) {
  .HomePage-subtitle {
    font-size: 1em;
  }
}

.HomePage-description {
  color: #555555;
  font-family: 'Source Serif Pro', serif;
  padding: 1em 0;
}
.LinkList {
    text-align: center;
    margin: 0.5em 0;
    flex: 1 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
@media only screen and (min-width: 1024px) {
    .LinkList {
        flex: none;
        justify-content: flex-end;
    }
}

.LinkList-group {
    display: flex;
    margin: 0.5em 0;
}

.LinkList-link-container {
    margin: 0 0.5em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
@media only screen and (min-width: 1024px) {
    .LinkList-link-container {
        justify-content: center;
    }
}

.LinkList-divider {
    margin: 0 1em;
}
@media only screen and (min-width: 1024px) {
    .LinkList-divider {
        height: 28px;
        border-right: 1px solid rgb(236, 236, 236);
    }
}

.LinkList-link {
}

.LinkList-link-img {
    width: 32px;
    transition: -webkit-filter ease-in-out 200ms;
    transition: filter ease-in-out 200ms;
    transition: filter ease-in-out 200ms, -webkit-filter ease-in-out 200ms;
}
.LinkList-link-img:hover {
    -webkit-filter: none;
            filter: none;
}
@media only screen and (min-width: 1024px) {
    .LinkList-link-img {
        -webkit-filter: opacity(45%) grayscale(100%);
                filter: opacity(45%) grayscale(100%);
    }
    .LinkList-color-img {
        -webkit-filter: none;
                filter: none;
    }    
}

.LinkList-round-img {
    border-radius: 50%;
}

.LinkList-link-title {
    font-size: 0.75em;
}
@media only screen and (min-width: 1024px) {
    .LinkList-link-title {
        display: none;
    }
}
.Header {
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    height: 196px;
    margin: 0.35em;
    background-color: white;
    border-radius: 10px;
    transition: background-color cubic-bezier(0.86, 0, 0.07, 1) 300ms;
}

.Header-menu-open {
    background-color: #FBF4F4;
}

.Header-img-a {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 72px;
    line-height: 0;
    width: 48px;
    margin: 0 auto;
    display: block;
    border-radius: 50%;
}

.Header-img {
    width: 48px;
    border-radius: 50%;
}

.Header-title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 1.5em;
    text-align: center;
    margin: 1em 0 0.667em 0;
}

.Header-underline {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0px;
    width: 120px;
    margin: 0 auto;
    border-bottom: 1px solid black;
    transition: width ease-in 200ms;
}

.Header-underline-long {
    width: 240px;
}

.Header-menu-list {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 40px;
    height: 48px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.Header-menu-img {
    position: static;
    margin-left: 0.75em;
    margin-right: 0.75em; 
}

.Toolbar {
    padding: 0.65em;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Toolbar-title {
    color: #666666;
    text-align: center;
    margin-right: 32px;
}
.MenuButton-wrapper {
    height: 24px;
    width: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.MenuButton {
    width: 24px;
    height: 24px;
    background-image: url('/assets/menu_icon.png');
    background-size: contain;
}

.MenuButton-open {
    width: 18px;
    height: 18px;
    background-image: url('/assets/menu_icon_open.png');
}

.InfoPage {
    font-size: 0.875em;
}

.InfoPage-divider {
    height: 0;
    width: 0;
    border-left: 50vw solid rgba(248, 248, 248, 0.5);
    border-right: 50vw solid rgba(248, 248, 248, 0.5);
    border-top: 30px solid transparent;
    border-bottom: 30px solid rgba(248, 248, 248, 1);
}

.InfoPage-section {
    padding: 0 2em;
}

.InfoPage-projects {
    padding: 1em 2em 4em 2em;
    background-color: #F8F8F8; 
}

.InfoPage-header {
    font-size: 1.25em;
    padding: 0.5em 0;
}

.GeneralInfo {
    margin: 2em 0 1em 0;
}

.GeneralInfo-description {
    text-align: center;
}

.GeneralInfo-info {
    margin: 2em 0 1em 0;
    padding: 1em 1.5em;
    background-color: #F8FBFD;
    border-radius: 8px;
}

.GeneralInfo-info-item {
    margin: 0.5em 0;
    display: flex;
    justify-content: space-between;
}

.GeneralInfo-info-label {
    color: #4e4e4e;
}

.GeneralInfo-info-value {
    text-align: end;
}

.ProjectInfo {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ProjectInfo-img {
    width: 50px;
    height: 100px;
    margin: 1em auto;
}

.ProjectInfo-title {
    margin: 1em 0;
}
a.ProjectInfo-title {
    color: #0085FF;
}

.ProjectInfo-description {
    margin: 1em 0;
}
.Section {
    margin: 1em 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.Section-title {
    font-size: 1.05em;
    margin: 0.5em 0;
}

.Section-underline {
    width: 90px;
    margin: 0 auto 1.5em auto;
    border-bottom: 1px solid black;
}

.Section-img {
    width: 125px;
    height: 125px;
    margin: 0.5em auto 1em auto;
}

.SubSection {
    margin: 0.5em 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.SubSection-title {
    color: #727272;
    margin-bottom: 1em;
}

.SubSection-paragraph {
    margin: 0.5em 0;
}

.SubSection-breakdown {
    color: #5C5C5C;
    padding: 1em;
    margin: 0.5em 0;
    background-color: #EBF6FE;
    border-radius: 8px;
}

.SubSection-breakdown-point:empty {
    height: 1em;
}
